* {
  margin: 0;
  padding: 0;
  font-family: PingFang;
}
a {  
  color: #000000;
}
hr {
  margin-top: 5px;
  margin-bottom: 5px;
}

/* 版心，浮动的公共类 */
.w {
  width: 1190px;
  /* width: 80%; */
  margin: 0 auto;
  position: relative;
}
.bg_center {
  text-align: center
}
h1, h2 {
  font-weight: 300;
  letter-spacing: 2px;
}
h1 span {
  font-size: 0.8em;
  color: rgb(100, 100, 100);
}
h2 {
  margin-bottom: 20px;
}
h2 p {
  font-size: 0.7em;
  text-align: center;
}
section {
  width: 600px;
  margin: auto;
  letter-spacing: 2px;
}
li {
  list-style: none;
  letter-spacing: 2px;
}
.banner {
  background-color: rgb(255, 255, 255);
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 57px;
  /* width: 5120px; */ /* 宽度改变时会逐渐向一边偏移 */
  width: 100%;
  z-index: 100;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: rgb(100, 100, 100);
}
.banner_cushion {
  height: 57px;
}
.banner_end {
  height: 57px;
  border-top-style: solid;
  border-top-width: 1px;
  border-top-color: rgb(100, 100, 100);
}
.banner_end_brief {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  color: rgb(100, 100, 100);
  font-weight: 300;
}
#logo {
  height: 30px;
  margin-top: 13px;
  margin-bottom: 10px;
  margin-left: 50px;
}
.tap {
  position: absolute;
  top: 16px;
  /* left: 550px; */
  left: 50%;
  /* display: flex; */
  /* justify-content: space-between; */
}
.taplink {
  /* color: rgb(201, 209, 228); */
  color: rgb(100, 100, 100);
  font-size: 1em;
  text-decoration: none;
  margin-left: 15px;
  padding-bottom: 18px;
}
.taplink:hover {
  /* color:rgb(97, 218, 251); */
  color:rgb(203, 59, 0);
  -webkit-transition: color  0.2s;
  transition: color  0.2s;
  text-decoration: none;
}
.router-link-active{ /* router-link标签在选中的时候，会自动给整个标签添加一个router-link-active的class */
  color: rgb(203, 59, 0);
  border-bottom-style: solid;
  border-bottom-color: rgb(203, 59, 0);
  border-bottom-width: 3px;
  /* transition: border-bottom-color 1s; */
}


